.wordle-info {
	width: 500px;

	margin: 0 auto;
}

.info-header {
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
}

.info-body {
	padding: 16px;
	padding-top: 0px;
	width: 500px;
	box-sizing: border-box;
}

.info-title {
	text-align: center;
	font-weight: 700;
	font-size: 16px;
	letter-spacing: 0.5px;
	color: var(--color-tone-1);
	font-family: 'Clear Sans', 'Helvetica Neue', Arial, sans-serif;
	margin-bottom: 10px;
	margin-top: 10px;
}

.info-section {
	border-bottom: 1px solid var(--color-tone-4);
}

.info-section-text {
	display: block;
	margin-block-start: 1em;
	margin-block-end: 1em;
	margin-inline-start: 0px;
	margin-inline-end: 0px;
	font-family: 'Clear Sans', 'Helvetica Neue', Arial, sans-serif;
	font-size: 14px;
	color: var(--color-tone-1);
}

.info-section-description {
	font-size: 12px;
	color: var(--color-tone-2);
	display: block;
}
