.wordle-settings {
	width: 500px;

	margin: 0 auto;
}

.settings-header {
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
}

.close-icon {
	position: absolute;
	right: 0;
	user-select: none;
	cursor: pointer;
	color: var(--color-tone-1);
}

.settings-title {
	text-align: center;
	font-weight: 700;
	font-size: 16px;
	letter-spacing: 0.5px;
	color: var(--color-tone-1);
	font-family: 'Clear Sans', 'Helvetica Neue', Arial, sans-serif;
	margin-bottom: 15px;
	margin-top: 10px;
}

.section {
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-bottom: 1px solid var(--color-tone-4);
	padding: 16px 0;
}

.section-text {
	display: block;
}

.section-name {
	color: var(--color-tone-1);
	font-size: 18px;
	font-family: 'Clear Sans', 'Helvetica Neue', Arial, sans-serif;
	display: block;
}

.section-description {
	font-size: 12px;
	color: var(--color-tone-2);
	display: block;
}

.switch {
	height: 20px;
	width: 32px;
	background: var(--color-tone-3);
	border-radius: 999px;
	display: block;
	position: relative;
}

.knob {
	display: block;
	position: absolute;
	left: 2px;
	top: 2px;
	height: calc(100% - 4px);
	width: 50%;
	border-radius: 8px;
	background: var(--white);
	transform: translateX(0);
	transition: transform 0.3s;
}

.switch-active {
	background: var(--color-correct);
}

.knob-active {
	transform: translateX(calc(100% - 4px));
}
