.wordle-keyboard {
	display: block;
	height: var(--keyboard-height);
	width: fit-content;
	margin: 0 auto;
	box-sizing: border-box;
}

.wordle-keyboard-row {
	margin-bottom: 8px;
	display: flex;
	width: '100%';
	flex-direction: row;
	flex-wrap: nowrap;
}

.wordle-keyboard-last-row {
	display: flex;
	width: '100%';
	flex-direction: row;
	flex-wrap: nowrap;
}

.wordle-wide-key {
	background-color: var(--key-bg);
	color: var(--key-text-color);
	flex: 1.5;
	display: flex;
	margin-right: 6px;
}
