.wordle-game-container {
	width: 100%;
}

.wordle-button {
	border: 0;
	border-radius: 4px;
	width: 43px;
	height: 58px;
	font-family: 'Clear Sans', 'Helvetica Neue', Arial, sans-serif;
	font-size: 13.3333px;
	line-height: 18px;
	font-weight: 700;
	letter-spacing: 0.05rem;
	cursor: pointer;
	justify-content: center;
	align-items: center;
	user-select: none;
}

.wordle-restart {
	width: 200px;
	height: 50px;
	display: flex;
	background-color: var(--key-bg);
	color: var(--key-text-color);
}
