.wordle-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: var(--header-height);
	box-sizing: border-box;
	border-bottom: 1px solid var(--color-tone-4);
	width: 100%;
	margin: 0 auto;
}

.wordle-title {
	font-weight: 700;
	font-size: 36px;
	letter-spacing: 0.2rem;
	text-transform: uppercase;
	text-align: center;
	position: absolute;
	left: 0;
	right: 0;
	pointer-events: none;
	font-family: 'Clear Sans', 'Helvetica Neue', Arial, sans-serif;
	color: var(--color-tone-1);
	display: block;
}

.wordle-icon {
	background: none;
	border: none;
	cursor: pointer;
	padding: 0 4px;
}
